.checkbox-styled + label:before, .checkbox-styled + label:after { content: ''; border-radius: 50%; display: block; left: 0; position: absolute; top: 0; }

.checkbox-styled + label:before { border: 1px solid #fff; width: 17px; height: 17px; }

.checkbox-styled + label:after { background: #fff; display: none; width: 7px; height: 7px; margin: 5px; }

.checkbox-styled:checked + label:after { display: block; }

